#cy {
  width: 100%;
  height: calc(100vh - 9rem);
  background: #fafafa radial-gradient(#bebebe 1px, #0000 0) 0 0 / 18px 18px;
  margin: auto;
  position: relative;
}

body {
  font-family: helvetica;
  font-size: 14px;
}

.cyNode {
  min-height: 60px;
  text-align: center;
  color: #fff;
  color: #333637;
  cursor: pointer;
  border-style: solid;
  border-color: #333637;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  padding: 8px;
  font-weight: 700;
  box-shadow: 3px 3px 3px #4449;
}

.nodeTitle {
  margin-bottom: 5px;
  padding: 5px 0;
  font-size: 14px;
}

.trim {
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
  overflow: hidden;
}

.nodeValue {
  background-color: #ffffff80;
  border-radius: 20px;
  padding: 5px;
  font-size: 14px;
  box-shadow: 0 5px 4px -4px #777;
}

.nodeValue[value="undefined"] {
  display: none;
}

.goggleBtn[collapse="1"] {
  width: 16px;
  height: 16px;
  color: #333637;
  background-color: #fff;
  border-style: solid;
  border-color: #333637;
  border-radius: 16px;
  padding: 3px;
  font-weight: 700;
  position: absolute;
  bottom: -5px;
  right: -5px;
  box-shadow: 0 5px 4px -4px #777;
}

.goggleBtn[collapse="0"] {
  display: none;
}

.goggleBtn {
  box-sizing: content-box;
  text-align: center;
  flex-direction: column;
  place-content: center;
  display: flex;
}

.element-graphic {
  justify-content: center;
  align-items: center;
  display: inline-flex;
  box-shadow: 0 0 1px #0000001f, 0 1px 1px #0000003d;
}

.icon {
  max-width: 46px;
  height: auto;
  display: inline-block;
  -webkit-mask-size: cover;
  mask-size: cover;
}

